@define-mixin mobile {
  @media screen and (width <= $tablet - 1px) {
    @mixin-content;
  }
}

@define-mixin tablet {
  @media screen and (width >= $tablet) {
    @mixin-content;
  }
}

@define-mixin touch {
  @media screen and (width <= $desktop - 1px) {
    @mixin-content;
  }
}

@define-mixin desktop {
  @media screen and (width >= $desktop) {
    @mixin-content;
  }
}

@define-mixin widescreen {
  @media screen and (width >= $widescreen) {
    @mixin-content;
  }
}

@define-mixin unselectable {
  -webkit-touch-callout: none;
  user-select: none;
}
