@define-mixin message-color $name, $color {
  &.is-$(name) {
    & .message-header {
      background-color: $color;
    }
  }
}

.message-header {
  font-size: 0.7rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.message {
  @extend block;
  background-color: $background;
  border-radius: $radius;

  /* Loop colors */
  @mixin message-color dark, $dark;
  @mixin message-color info, $info;
  @mixin message-color warning, $warning;
  @mixin message-color danger, $danger;
}
