/* Generic Extends */
@define-extend block:not(:last-child) {
  margin-bottom: 20px;
}

.container {
  position: relative;
  & {
    @mixin desktop {
      margin: 0 auto;
      min-width: 960px;
      &.is-fluid {
        margin: 0 20px;
        min-width: none;
      }
    }
  }
}
